.popup{
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  right: 0;
  /* transition: opacity 2s ease; */
}

.popup__background{
  position: absolute;
  background-color: rgba(0,0,0,0.6);
  width: 100vw;
  height:187vh;
  z-index: 2;
  cursor: pointer;
}

.popup__container{
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 30%;
  top: 20%;
  left: 35%;
  height: 60%;
  background-color: #fff;
  z-index: 3;
}

.popup__image{
    width: 100%;
    height: 40%;
    object-fit: cover;
}

/* .popup::before {
    position: absolute;
    content: "";
    background-color: #000000;
    height: 100%;
    width: 100%;
  } */

  .popup__title{
    color: #001F3D;
    margin-bottom: 0px;
  }

  .popup__text{
    color: #001F3D; 
  }
  
.popup__close-button {
    cursor: pointer;
    border: none;
    background: none;
    color: white;
    padding: 0;
    width: 32px;
    height: 32px;
  }
  
  .popup__close-button:before {
    position: fixed;
    content: " ";
    height: 35px;
    border: 3px solid white;
    color: white;
    background-color: #fff;
    top: 16%;
    right: 34%;
    transform: rotate(45deg);
    z-index: 2;
  }
  
  .popup__close-button:after {
    position: fixed;
    content: " ";
    height: 35px;
    border: 3px solid white;
    color: white;
    background-color: #fff;
    top: 16%;
    right: 34%;
    transform: rotate(-45deg);
    z-index: 2;
  }
  
  .popup__close-button_hover::after{
    opacity: 0.6;
  }

  .popup__close-button_hover::before{
    opacity: 0.6;
  }
  
  @media (max-width: 680px) {
    .popup__close-button {
      width: 20px;
      height: 20px;
    }
    .popup__close-button:before {
      height: 20px;
      border: 2px solid white;
    }
    .popup__close-button:after {
      height: 20px;
      border: 2px solid white;
    }
  }
  